.coinContainer{
  background-image: url("../../images/bg-card2.png");
  background-size: cover;
  background-color: #f4f4f4;
  padding: 10px 20px;
  -ms-flex-preferred-size: calc(30% - 25px);
      flex-basis: calc(30% - 25px);
  margin: 10px;
  border-radius: 10px;
  font-family: 'Days One', sans-serif;
}
.coinWrap1{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.coin-symbol{
  color: #ff8100;
  font-weight: bold;
}
.coin-title{
 color: #3c3c3c;
}
.coinWrap2{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}
.coin-price{
  font-size: 35px;
}
.coin-percent{
  color: green;
}
.coinWrap3 h6{
  color: grey;
}
.coinWrap3 p{
  font-size: 20px
}




@media only screen and (max-width: 1200px) {
  .coinContainer{
    -ms-flex-preferred-size: calc(50% - 80px);
        flex-basis: calc(50% - 80px);
    margin: 10px;
  }
}

@media only screen and (max-width: 690px) {
  .coinContainer{
    -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
    margin: 10px;
  }
}



